.ListMenu {
  position: relative;
  &:hover .list-item-wapper {
    display: block; }
  .title {
    padding: 0 16px;
    transition: color 0.3s, background 0.3s, border-color 0.3s;
    &:hover {
      color: #1DA1F2; }
    &.on {
      color: #1DA1F2; } }
  .list-item-wapper {
    display: none;
    position: absolute;
    z-index: 99;
    top: 16px;
    .list-item {
      display: flex;
      width: 190px;
      padding: 12px 16px 0;
      margin-top: 16px;
      box-shadow: 0 0 10px #ccc;
      background-color: #fff;
      border-radius: 4px; }
    .title {
      padding: 0;
      line-height: 30px;
      &:hover {
        color: #575757; } }
    ul {
      list-style: none;
      padding-left: 16px;
      li {
        line-height: 30px;
        cursor: pointer;
        &:hover {
          color: #1DA1F2;
          transition: color 0.3s, background 0.3s, border-color 0.3s; } } } } }
