body {
  background-color: #f5f8fa; }
hr {
  margin: 12px 0; }

.NavLink {
  a {
    display: inline-block;
    height: 32px;
    padding: 0 16px;
    border-radius: 4px;
    line-height: 32px;
    text-decoration: none;
    color: #8590a6;
    cursor: pointer;
    transition: color 0.3s, background 0.3s, border-color 0.3s;
    &.active {
      background: #1DA1F2;
      color: #fff;
      &:hover {
        color: #fff;
        background: #47bfff; } }
    .icon {
      font-size: 24px;
      vertical-align: middle;
      margin-right: 4px; }
    &:hover {
      color: #1DA1F2; } } }



.tabs {
  background-color: #1DA1F2;
  padding-bottom: 72px;
  padding-top: 32px;
  .container {
    width: 1200px;
    margin: 0 auto;
    padding: 16px;
    background-color: #fff; } }


.song {
  position: relative;
  top: -40px;
  margin: 0 auto;
  width: 1200px; }


.pagination {
  padding: 16px;
  background-color: #fff;
  text-align: center; }


.icon {
  width: 1em;
  height: 1em;
  vertical-align: -0.15em;
  fill: currentColor;
  overflow: hidden;
  font-size: 36px; }

.loader-wrapper {
  position: absolute;
  top: 0;
  height: 100%;
  width: 100%;
  background-color: rgba(255, 255, 255, 0.8);
  opacity: 1;
  -webkit-transition: opacity 0.2s ease-in;
  transition: opacity 0.2s ease-in;
  pointer-events: auto;
  z-index: 999;
  .loader {
    width: 100%;
    text-align: center;
    top: 50%;
    position: absolute;
    margin-top: -20px;
    line-height: 40px; } }
.message {
  display: flex;
  align-items: center;
  margin-left: 8px;
  .message-top {
    font-size: 14px;
    // font-weight: 600
    line-height: 1.2;
    color: #34495e; }
  .message-bottom {
    font-size: 12px;
    color: #8590a6; } }
.time-wapper {
  display: flex;
  .time-item {
    display: flex; }
  .active {
    color: #1DA1F2;
    &::affter {} } }

.set {
  display: flex; }
